<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process42"></a>

<p><br></p>
<p><span class="rvts17">基础属性</span></p>
<p><img width="295" height="763" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber132.jpg"><span class="rvts18"></span>
<br><span class="rvts21">角色的所有基础属性值都以</span><span class="rvts20">10</span><span class="rvts21">点开始，玩家在建立角色选择职业的时候，可以分配</span><span class="rvts20">3</span><span class="rvts21">点额外的属性点，之后角色每升一级，玩家就会得到</span><span class="rvts20">2</span><span class="rvts21">点的额外属性点分配。</span>
<br><span class="rvts21">然而，每个属性点的分配上限是</span><span class="rvts20">40</span><span class="rvts21">点，换句话说，玩家最多在同一基础属性点上最多分配</span><span class="rvts20">30</span><span class="rvts21">点。但是玩家可以通过装备提升额外的属性点数。</span>
<br><span class="rvts21">另外，孤狼（</span><span class="rvts20">Lone Wolf</span><span class="rvts21">）天赋允许玩家可以双倍提升基础属性点和绝大多数战斗技能。</span>
<br><span class="rvts43"></span>
<br><span class="rvts33">1. </span><span class="rvts29">力量（</span><span class="rvts33">strength </span><span class="rvts29">）</span>
<br><span class="rvts21">力量值可以增加具备力量需求的武器或技能的伤害。力量还可以增加携带重量以及搬动更重物体的能力。</span>
<br><span class="rvts21">每点力量值的增加会使得以下属性获得加成：</span>
<br><span class="rvts20">* +5% </span><span class="rvts21">伤害（力量型）</span>
<br><span class="rvts20">* +10 </span><span class="rvts21">额外携带重量</span>
<br><span class="rvts20">* +7.5 </span><span class="rvts21">额外物体搬动重量</span>
<br><span class="rvts21">注：在力量值低于</span><span class="rvts20">10</span><span class="rvts21">点以下，那么每降低</span><span class="rvts20">1</span><span class="rvts21">点力量值会减少</span><span class="rvts20">5%</span><span class="rvts21">的力量型伤害值。</span>
<br><span class="rvts43"></span>
<br><span class="rvts33">2. </span><span class="rvts29">灵巧（</span><span class="rvts33">finesse </span><span class="rvts29">）</span>
<br><span class="rvts21">灵巧值可以增加具备灵巧需求的武器或技能的伤害。</span>
<br><span class="rvts21">每点灵巧值的增加会使得以下属性获得加成：</span>
<br><span class="rvts20">* +5% </span><span class="rvts21">伤害（灵巧型）</span>
<br><span class="rvts21">注：在灵巧值低于</span><span class="rvts20">10</span><span class="rvts21">点以下，那么每降低</span><span class="rvts20">1</span><span class="rvts21">点灵巧值会减少</span><span class="rvts20">5%</span><span class="rvts21">的灵巧型伤害值。</span>
<br><span class="rvts43"></span>
<br><span class="rvts33">3. </span><span class="rvts29">智力（</span><span class="rvts33">intellegence </span><span class="rvts29">）</span>
<br><span class="rvts21">智力值可以增加具备智力需求的武器或技能的伤害。</span>
<br><span class="rvts21">每点智力值的增加会使得以下属性获得加成：</span>
<br><span class="rvts20">* +5% </span><span class="rvts21">伤害（智力型）</span>
<br><span class="rvts21">注：在智力值低于</span><span class="rvts20">10</span><span class="rvts21">点以下，那么每降低</span><span class="rvts20">1</span><span class="rvts21">点智力值会减少</span><span class="rvts20">5%</span><span class="rvts21">的智力型伤害值。</span>
<br><span class="rvts43"></span>
<br><span class="rvts33">4. </span><span class="rvts29">体质（</span><span class="rvts33">constitution</span><span class="rvts29">）</span>
<br><span class="rvts21">体质值可以增加角色的最大生命值。</span>
<br><span class="rvts21">每点体力值的增加会使得以下属性获得加成：</span>
<br><span class="rvts20">* +7% </span><span class="rvts21">额外生命值</span>
<br><span class="rvts21">注：在体质值低于</span><span class="rvts20">10</span><span class="rvts21">点以下，那么每降低</span><span class="rvts20">1</span><span class="rvts21">点体力值会减少</span><span class="rvts20">7%</span><span class="rvts21">的智力型伤害值。</span>
<br><span class="rvts43"></span>
<br><span class="rvts33">5. </span><span class="rvts29">谋略（</span><span class="rvts33">wits </span><span class="rvts29">）</span>
<br><span class="rvts21">谋略值将会增加角色的致命一击（暴击）几率，增加战斗中的顺序优先级，以及侦查到陷阱和宝藏的能力。</span>
<br><span class="rvts21">每点谋略值的增加会使得以下属性获得加成：</span>
<br><span class="rvts20">* +1 </span><span class="rvts21">顺序优先</span>
<br><span class="rvts20">* +1% </span><span class="rvts21">暴击几率</span>
<br><span class="rvts43"></span>
<br><span class="rvts33">6. </span><span class="rvts29">记忆力（</span><span class="rvts33">memory </span><span class="rvts29">）</span>
<br><span class="rvts21">记忆力将决定在同一时间内，玩家可以使用的技能的数量多少。不同于之前的版本，原罪</span><span class="rvts20">2</span><span class="rvts21">在学习技能数量方面并没有限制，只是在技能的可用次数上进行了限制。</span>
<br><span class="rvts21">因此，大多数的技能只需要</span><span class="rvts20">1</span><span class="rvts21">个记忆槽，但是更厉害的技能会需要两个以上的记忆槽以及更多的源力。</span>
<br><span class="rvts21">每点记忆力值的增加会使得以下属性获得加成：</span>
<br><span class="rvts20">* +1 </span><span class="rvts21">记忆槽</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



